import React from 'react'
import {Modal} from 'antd'
import components from './../../components/index'
import {connect} from './../../common/util/index'
import * as actions from './state/action'
import './index.less'

const {ChatPane, SaveInfo, History, QuillEditor} = components

class LeftBody extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            showSaveInfo: false,
            showHistory: false,
        }
    }

    componentDidMount() {
        // joinChannel
        // 
    }

    render() {
        const {showHistory,showSaveInfo} = this.state
        return (
            <div className="main">
                <div className="main-header">{/**/}
                        <div className="operation">
                            <a onClick={()=>{this.setState({showSaveInfo: true})}}></a>
                            <a onClick={()=>{this.setState({showHistory: true})}}></a>
                        </div>
                        <Modal visible={showSaveInfo} footer={null} title=" channel1" onCancel={()=>{this.setState({showSaveInfo: false})}}><SaveInfo /></Modal>
                        <Modal visible={showHistory} footer={null} title="channel1 " 
                            onCancel={()=>{this.setState({showHistory: false})}}
                            width={1200}
                        ><History /></Modal> {/*modaltitle*/}
                </div>
                <div className="main-body">
                    <div className="main-body-left">
                        <QuillEditor />
                    </div>
                    <div className="main-body-right">
                        <ChatPane />
                    </div>
                </div> 
        </div>)
    }
}

export default connect((state)=>{
    return {
        ...state.channelList
    }
},actions)(LeftBody)


