import React from 'react'
import ReactDom from 'react-dom'
import {createStore, applyMiddleware, compose} from 'redux'
import {HashRouter,Route, Switch} from 'react-router-dom'
import {Provider} from 'react-redux'
import thunk from 'redux-thunk'
import MainLayout from './containers/mainLayout/index'
import PersonalPage from './containers/personalPage/index'
import Login from './containers/login/index'
import rootReducer from './rootReducer'

const App =()=> (
    <Switch>
        <Route exact path="/" component={Login} />
        <Route path="/mainPage" component={MainLayout} />
        <Route path="/personalPage" component={PersonalPage}/>
    </Switch>
)

// initialState
const store = createStore(rootReducer, compose(
    applyMiddleware(thunk),
    window.devToolsExtension ? window.devToolsExtension() : f => f //redux-devtool
))

ReactDom.render(
    <Provider store={store}>
        <HashRouter>
            <App />
        </HashRouter>
    </Provider>,
    document.getElementById('root')
)