import React from 'react'
import {Divider, Table} from 'antd'
import './index.less'

export default class History extends React.Component {
    constructor(props){
        super(props)
    }

    render() {
        const historyInfo = [{
            commitInfo: 'longString,',
            modifyDate: '2018-07-15 22:30:15',
            modifyer: {
                nickname: 'nickname',
                nodeid: 'nodeid'
            },
            version: '',
            fileHash:''
        }]
        const columns = [{
            title: "",
            dataIndex: "commitInfo",
            key:"commitInfo", // hover
            width:'25%'
        },{
            title:"",
            dataIndex:'fileHash',
            key:'fileHash',
            width:'20%'
            // modal
        },{
            title:"",
            dataIndex:'modifyer',
            key:'modifyer', 
            width:'30%',
            render: (text)=>{
                // 
                return <div className="modifyerInfo">
                    <span>{text['nickname']}</span>
                    <Divider type="vertival" />
                    <span>{text['nodeid']}</span>
                </div>
            }
        },{
            title: "",
            dataIndex:'version',
            key:'version',
            width: '10%',
        },{
            title: "",
            dataIndex: 'modifyDate',
            key:'modifyDate',
            width:'20%',
        },]
        return (
            <div className="history">
                <Table borderd
                    columns={columns} 
                    dataSource={historyInfo}
                    rowKey={record=>record.fileHash}
                />
            </div>
        )     
    }
}