import React from 'react'
import Orbit from 'orbit_' // orbit_core,
import OrbitDB from 'orbit-db'
import IPFS from 'ipfs'
import {Form, Input, Icon} from 'antd'
import {connect} from './../../common/util/index'
import * as actions from './state/action'
import './index.less'

const FormItem = Form.Item
const inputLayout = {
    labelCol: {
        span: 6,
    },
    wrapperCol: {
        span: 18,
    }
}

class LoginPage extends React.Component {
    constructor(props){
        super(props)
        this.orbit = this.props.orbit
    }

    componentDidMount() {
        
    }

    handleLogin =()=>{
        // database
        // usernamedatabase
        // ipfs settingipfs
        const {form, publicInfo,startLogin} = this.props
        const username = form.getFieldValue('username')
        console.log(publicInfo)
        startLogin(username)
        console.log(this.props.publicInfo)
        window.location.href = "./#/mainPage"
    }
 
    render() {
        const {getFieldDecorator} = this.props.form
        return (
            <div className="login">
                <div className="loginPage">
                    <div className="loginPage-slogan">
                        <div className="loginPage-slogan-logo" />
                        <p className="loginPage-slogan-title">IPFS</p>
                    </div>
                    <div className="loginPage-form">
                        <Form style={{width:'100%'}}>
                            <FormItem label="" {...inputLayout} style={{margin:0,padding:0}}>
                                {
                                    getFieldDecorator('username',{
                                        rules:[{
                                            required: true,
                                            message: 'Login needs your username firstly'
                                        }]
                                    })(<Input 
                                            className="loginPage-form-username" 
                                            placeholder="input your username"
                                        />)   
                                }
                            </FormItem>
                        </Form>
                        <div className="loginPage-form-tip">
                            <Icon type="exclamation-circle-o" />
                            <span>GethGeth</span>
                        </div>
                        <div 
                            className="loginPage-form-login"
                            onClick ={this.handleLogin}
                        ><a>Sign In</a></div>
                    </div>
                </div>
            </div>
        )
    }
}

const Login =  Form.create()(LoginPage)

export default connect((state)=>{
    return {
        ...state.login
    }
},actions)(Login)


